#demo {
  background-color: white;
  text-align: center;
  padding: 2%;
}

#demo h2 {
  font-weight: 600;
}

#music-select {
  padding: 0 11%;
}

#style-select {
  padding: 0 11%;
}

#select-box {
  width: 300px;
  height: 200px;
  float: left;
  text-align: center;
}

#select-box h3 {
  margin: 30px auto 0 auto;
  font-weight: 600;
}

#music-box {
  float: right;
  width: 750px;
}

.image-wrapper {
  width: 150px;
  height: 150px;
  display: inline-block;
  margin: 4%;
}

.btn {
  margin: 5%;
  border: 1px solid black;
  border-radius: 10px;
}

.select {
  background-color: antiquewhite;
}

.on {
  border: 3px outset grey;
}

.off {
  filter: blur(1.5px);
}

.cb {
  clear: both;
}

#cross-wrapper {
  margin-left: 21%;
}

#cross2 {
  display: inline-block;
	width: 50px;
  height: 50px;
  margin: 2%;
	position: relative;
	cursor: pointer;
}

#cross2 span::before,
#cross2 span::after {
	display: block;
	content: "";
	position: absolute;
	top: 50%;
	left: 50%;
	width: 84%;
	height: 16%;
	background: black;
}
#cross2 span::before {
	transform: rotate(-45deg);
}
#cross2 span::after {
	transform: rotate(45deg);
}

.arrow-result
{
  position: relative;
  bottom: -2rem;
  left: 50%;
  margin-top: 30px;
  margin-left:-20px;
  width: 40px;
  height: 40px;

  /**
   * Dark Arrow Down
   */
  background-image: url(data:image/svg+xml;base64,);
  background-size: contain;
}

.bounce {
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}

.audio-result {
  margin-top: 5%;
}

.new-music {
  margin-bottom: 20px;
  width: 250px;
}

